  }
  .configure{
    font-size: 13px;
    color: #1B1B23;
    padding-bottom: 3px;
  }
  .type{
    font-size: 13px;
    color: #1B1B23;
    padding-bottom: 3px;
  }
  .price{
    color: #EA7865;
    font-size: 16px;
  }
  .mint-popup {
    width: 100%;
    height: 35%;
    text-align: center;
  }
  .filter-popup {
    padding-left: 10px;
    padding-right: 10px;
    text-align: left;
    width: 100%;
    height: 100%;
  }
  .filter{
    background-color: white;
    padding-right: 20px;
    padding-left: 20px;
    border-radius: 25px;
    transform:translate(-50%,-50%);
    position : absolute;
    bottom: 100%;
  }
  .btnconfirm{
    left: 10%;
