  }
 .mapfilter{
   background-color: white;
   padding-right: 20px;
   padding-left: 20px;
   border-radius: 25px;
   left: 50%;
   transform:translate(-50%,-50%);
   position : fixed;
   bottom: 20px;
 }
  .active .selectstatus{
    background-color: #EA7865;
    height: 6px;
    margin-left: 2px;
    margin-right: 2px;
  }
  .selectstatus{
    background-color: white;
