    border-radius: 5px;
    background-color: transparent;
    color: #409EFF;
  }
  .icon{
    border-radius: 50%;
  }
  .mint-popup-wechat {
    width: 120px;
    height: 120px;
    margin-top: 35px;
    border-radius: 4px;
    padding-top: 3px;
    padding-left: 2px;
    transform: translate(60px, 0);
  }
  .wechatimg{
    width: 115px;
    height: 115px;
  }
</style>



